html,
body {
    font-family: Arial, Helvetica, sans-serif;
    background-color: lightgray;
}

.pagehead {
    margin: 0px;
    margin-bottom: 10px;
    text-align: center;
    color: blue;
}

.main {
    display: flex;
    flex-direction: column;
    height: 92Vh;
    overflow-y: scroll;
}

.post {
    margin-left: 30%;
    margin-bottom: 2%;
    display: flex;
    flex-direction: column;
    align-content: center;
    justify-content: center;
    padding: 25px;
    width: 40%;
    background-color: white;
    border: 1px solid darkgrey;
    box-sizing: border-box;
    border-radius: 15px;
}

.post-head {
    margin: 0px;
    font-size: 1.5em;
    font-weight: 600;
}

h3 {
    margin: 0px;
    margin-top: 10px;
    margin-bottom: 10px;
}

p {
    margin: 0px;
    margin-top: 10px;
    margin-bottom: 10px;
    font-size: 1.2em;
}


.post-pic {
    width: 100%;
    height: auto;
}

.orig-link {
    margin-top: 10px;
    text-decoration: none;
    color: fuchsia;
    font-size: 1.1em;
}